<template>
   <div id="app">
      <h1 class="title">{{msg}}</h1>
      <div class="inputBox" v-bind:class={focus:isShow}><input type="text" v-model="todo" v-on:keyup.enter="saveInArr()" v-on:focus="addFieldFocus()" v-on:blur="addFieldBlur()" name="todo"/></div>
      <div class="list">
          <ul>
              <li v-for="(item, index) in todoArr" v-bind:class="{finished:item.status}">
                <a href="javascript:;" class="todoItem" v-on:click="toggleStatus(item)"><input type="checkbox" v-bind:name="'item'+index" v-bind:checked="item.status">{{item.todo}}</a><span>Finished</span><a class="closeBtn" href="javascript:;" v-on:click="deleteItem(item)"><i class="fa fa-close" aria-hidden="true"></i></a>
              </li>
          </ul>
      </div>
   </div>
</template>
<script type="text/javascript">
import Store from './store'
export default {
  data:function(){
    return {
      msg:"Todo List",
      todoArr:Store.fetch(),
      todo:"",
      isShow:false
    }
  },
  methods:{
    saveInArr:function(){
        this.todoArr.push(
          {
             todo:this.todo,
             status:false
          }
        );
        this.todo="";
    },
    toggleStatus:function(item){
      item.status=!item.status;
    },
    addFieldFocus:function(){
      this.isShow=true;
    },
    addFieldBlur:function(){
       this.isShow=false;
    },
    deleteItem:function(item){
      for(var x in this.todoArr){
        if (this.todoArr[x]==item) {
          this.todoArr.splice(x,1);
          return;
        }
      }
    }
  },
  watch:{
    todoArr:{
      handler:function(){
        Store.save(this.todoArr);
      },
      deep:true
    }
  }
}
</script>
<style type="text/css">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #434343;
  font-family:"Microsoft Yahei","Hiragino Sans GB","Helvetica Neue",Helvetica,tahoma,arial,"WenQuanYi Micro Hei",Verdana,sans-serif,"\5B8B\4F53";
  margin-top: 60px;
}
.title{
  text-align: center;
  margin-bottom:10px;
}
.inputBox{
    margin: 0 auto;
    width: 630px;
    height: 40px;
    border-radius: 10px;
    border: 1px solid #CFCFCF;
    font-size: 18px;
}
.focus{
      background-color: #fafffe;
      border-color: #3bb4f2;
      outline: 0;
      -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 5px rgba(59,180,242,.3);
      box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 5px rgba(59,180,242,.3);
}
.focus input{background-color:#fafffe }
.inputBox input{
    width: 610px;
    line-height: 40px;
    margin-left: 10px;
    font-size: 14px;
    color: #000;
    font-weight: 500;
}
.list{
  width: 610px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  font-size: 18px;
}
.list a{
  text-decoration: none;
  color: #000;
}
.finished label{
  padding-right: 20px;
  position: relative;
}
.list input{
  margin-right: 5px;
}
.finished .todoItem{
  text-decoration: line-through;
  margin-right: 10px;
}
.list span{display: none;}
.list .closeBtn{display: none;}
.list .finished span{
  display: inline;
  background: #3eba35;
  color: #fff;
  border: 1px solid #3eba35;
  border-radius: 9px;
  font-size: 12px;
  padding: 1px 9px;
}
.list .finished .closeBtn{
  display: inline;
  margin-left: 5px;
}
@font-face {
  font-family: 'FontAwesome';
  src: url('./assets/fonts/fontawesome-webfont.eot?v=4.7.0');
  src: url('./assets/fonts/fontawesome-webfont.eot?#iefix&v=4.7.0') format('embedded-opentype'), url('./assets/fonts/fontawesome-webfont.woff2?v=4.7.0') format('woff2'), url('./assets/fonts/fontawesome-webfont.woff?v=4.7.0') format('woff'), url('./assets/fonts/fontawesome-webfont.ttf?v=4.7.0') format('truetype'), url('./assets/fonts/fontawesome-webfont.svg?v=4.7.0#fontawesomeregular') format('svg');
  font-weight: normal;
  font-style: normal;
}
.fa {
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.fa-close:before,
.fa-times:before {
  content: "\f00d";
  color: #ff5400;
}
</style>



